import React from 'react';
import Rem_Square from './Square'
class Rem_Memo extends React.Component {
    constructor(props){
        super(props);
        this.state={
            num_rows:this.props.string_1.length+2,
            num_cols:this.props.string_2.length+2,
            memo:this.create_memo(),
            grid:this.getInitialGrid(),
            speed:'slow',
        }
        this.update_speed=this.update_speed.bind(this);
    }

    animateDijkstra() {
        var increment=1200;
        var lag=700;
        console.log(this.state.speed)
        if(this.state.speed==='slow'){
          console.log('slow')
        }
        else if(this.state.speed==='medium'){
          increment=500;
          lag=275;
        }
        else if(this.state.speed==='fast'){
          increment=350;
          lag=120;
        }
        console.log('increment ',increment,' lag ',lag)
        let counter=1;
        
        for (let i = 2; i < this.state.grid.length; i++) {
          for(let k=2;k<this.state.grid[0].length;k++){
            
            if(this.props.string_1[i-2]==this.props.string_2[k-2]||this.props.string_2[k-2]=='.'){
                setTimeout(() => {
                    document.getElementById(`node-${i}-${k}`).className ='node node-finish';
                    document.getElementById(`node-${i-1}-${k-1}`).className ='node node-dev';
                    document.getElementById(`node-${i}-${k}`).style ={color:'black',};
                    document.getElementById(`node-${0}-${k}`).className ='node node-prev';
                    document.getElementById(`node-${i}-${0}`).className ='node node-prev';
                    document.getElementById('1234').className ='if_statement_1_alt';
                }, (increment*counter));
                setTimeout(() => {
                    document.getElementById(`node-${i}-${k}`).className ='node';
                    document.getElementById(`node-${i-1}-${k-1}`).className ='node';
                    document.getElementById(`node-${0}-${k}`).className ='node';
                    document.getElementById(`node-${i}-${0}`).className ='node';
                    document.getElementById('1234').className ='if_statement_1';
                }, (increment*counter + lag));
            }
            else if(this.props.string_2[k-2]==='*'){
              if(this.props.string_2[k-3]===this.props.string_1[i-2]||this.props.string_2[k-3]==='.'){
                setTimeout(() => {
                  document.getElementById(`node-${i}-${k}`).className ='node node-finish';
                  document.getElementById(`node-${i-1}-${k}`).className ='node node-start';
                  document.getElementById(`node-${i}-${k}`).style ={color:'black',};
                  document.getElementById(`node-${0}-${k}`).className ='node node-prev';
                  document.getElementById(`node-${0}-${k-1}`).className ='node node-dev';
                  document.getElementById(`node-${i}-${0}`).className ='node node-dev';
                  document.getElementById('5678').className ='if_statement_2_alt';
                }, (increment*counter));
                setTimeout(() => {
                    document.getElementById(`node-${i}-${k}`).className ='node';
                    document.getElementById(`node-${i-1}-${k}`).className ='node';
                    document.getElementById(`node-${i}-${0}`).className ='node';
                    document.getElementById(`node-${0}-${k}`).className ='node';
                    document.getElementById(`node-${0}-${k-1}`).className ='node';
                    document.getElementById('5678').className ='if_statement_2';
                }, (increment*counter + lag));
              }
              else{
                setTimeout(() => {
                  document.getElementById(`node-${i}-${k}`).className ='node node-finish';
                  document.getElementById(`node-${i}-${k-2}`).className ='node node-start';
                  document.getElementById(`node-${i}-${k}`).style ={color:'black',};
                  document.getElementById(`node-${0}-${k}`).className ='node node-prev';
                  document.getElementById('5678').className ='if_statement_2_alt';
                }, (increment*counter));
                setTimeout(() => {
                    document.getElementById(`node-${i}-${k}`).className ='node';
                    document.getElementById(`node-${i}-${k-2}`).className ='node';
                    document.getElementById(`node-${0}-${k}`).className ='node';
                    document.getElementById('5678').className ='if_statement_2';
                }, (increment*counter + lag));
              }
            }
            else {
                setTimeout(() => {
                    document.getElementById(`node-${i}-${k}`).className ='node node-finish';
                    document.getElementById(`node-${i}-${k}`).style ={color:'black',};
                }, (increment*counter));
                setTimeout(() => {
                    document.getElementById(`node-${i}-${k}`).className ='node';
                }, (increment*counter + lag));
                
            }
            counter++;
          }
        }
    }

    getInitialGrid = () => {
        const gridy = [];
        for (let row = 0; row < this.props.string_1.length+2; row++) {
          const currentRow = [];
          for (let col = 0; col < this.props.string_2.length+2; col++) {
            currentRow.push(this.createNode(col, row));
          }
          gridy.push(currentRow);
        }
        return gridy;
    };
      
    createNode = (col, row) => {
        return {
            col,
            row,
        };
    };
    
    create_memo(){
        
        const memo1 = []
        for (let r = 0;r<this.props.string_1.length+1;r++){
          memo1.push(Array(this.props.string_2.length+1).fill(false));
        }
        console.log('before',memo1)
        
        memo1[0][0]=true;
        for(let i=1;i<memo1[0].size;i++){
          if(this.props.string_2[i-1]==='*'&&memo1[0][i-2]){
              memo1[0][i]=true;
          }
        }
        

        for (let r = 1;r<this.props.string_1.length+1;r++){
          console.log('internal',memo1);
          for (let c = 1;c<this.props.string_2.length+1;c++){
            if(this.props.string_1[r-1]==this.props.string_2[c-1]||this.props.string_2[c-1]=='.'){
              memo1[r][c]=memo1[r-1][c-1];
            }
              
            else if(this.props.string_2[c-1]==='*'){
              if(this.props.string_2[c-2]===this.props.string_1[r-1]||this.props.string_2[c-2]==='.'){
                  memo1[r][c]=memo1[r-1][c];
              }
              memo1[r][c]=memo1[r][c]||memo1[r][c-2];
            }
          }
        }

        return memo1;
    };

    

    reseter(){
        console.log('here');
        for (let i = 2; i < this.state.grid.length; i++) {
            for(let k=2;k<this.state.grid[0].length;k++){
            
              document.getElementById(`node-${i}-${k}`).style.color='white' ;
            }
        }
    }
    update_speed(e){
      e.preventDefault();
      this.setState({
        speed:e.target.value,
      })
    }
    render() {
        let s = `if(string[r-1]==pattern[c-1]||pattern[c-1]=='.'){
   memo[r][c] = memo[r-1][c-1];
 }`
        let j = ` else if(this.props.string_2[c-1]==='*'){

   if(pattern[c-2]==string[r-1]||pattern[c-2]=='.'){
       memo1[r][c] = memo1[r-1][c];
   }

   memo1[r][c] = memo1[r][c]||memo1[r][c-2];
 }`
        return (
            <div>
              <label style={{marginLeft:'3.7%'}}>Memo-fill-speed: </label>
             <select style={{marginLeft:'0%'}}onChange={this.update_speed}>
              <option value="slow">slow</option>
              <option value="medium">medium</option>
              <option value="fast">fast</option>
             </select>
              <button onClick={() => this.animateDijkstra()} style={{marginLeft:'0%',marginTop:'1%'}}>
                Visualize REM memo
              </button>
              <div className="grid">
                {this.state.grid.map((row, rowIdx) => {
                  return (
                    <div key={rowIdx}>
                      {row.map((node, nodeIdx) => {
                        //const {rower, col} = node;
                        const rower = node.row;
                        const col = node.col;
                        let val='-';
                        let type='';
                        if(rower===0 && col>1){
                            val=this.props.string_2[col-2]
                            type='show_string'
                        }
                        else if(rower>1 && col===0){
                            val=this.props.string_1[rower-2]
                            type='show_string'
                        }
                        else if(rower>0&&col>0){
                            if(rower==1||col==1){
                                type='show'
                            }
                            else{
                                type='dont_show'
                            }
                            val=String(this.state.memo[rower-1][col-1]);
                        }
                        return (
                          <Rem_Square
                            key={nodeIdx}
                            row={rower}
                            col={col}
                            val={val}
                            typey={type}
                            >
                        </Rem_Square>
                        );
                      })}
                    </div>
                  );
                })}
              </div>
              <div style={{height:'250px', width:'415px', type:'relative', float:'left',marginLeft:'5%',marginTop:'0.7%'}} >
              <div id='1234' class='if_statement_1' style={{width:'415px'}}>
                <pre> {s}</pre>
              </div>
              <div id='5678' class='if_statement_2' style={{marginTop:'6.7%',width:'415px'}} >
                <pre>{j}</pre>
              </div>
              </div>
              <button onClick={() => this.reseter()} style={{marginLeft:'0%',marginTop:'2%'}}>
                reset REM memo
              </button>
            </div>
          );      

    }


}


export default Rem_Memo;