import React from 'react';
import Lcs_Form from './lcs/form'
import Rem_Form from './REM/form'
import Ed_Form from './Ed/form'
class Multiform extends React.Component {
    constructor(props){
        super(props);
        this.state={
            algorithm:'Longest Common Subsequence',
        }
        this.change_handler1 = this.change_handler1.bind(this);
    }

change_handler1(event) {
    this.setState({
        algorithm: event.target.value,
    });

}


render() {
    let compy;
    if(this.state.algorithm==='Longest Common Subsequence'){
        compy=(
            <div>
            <h2><p>We will visualise the Longest common Subsequence problem</p></h2>
            <a href='https://leetcode.com/problems/longest-common-subsequence/' target="_blank">Problem statement (leetcode)</a>
            <Lcs_Form></Lcs_Form>
            </div>
        );
    }
    else if(this.state.algorithm==='Regular Expression Matching'){
        compy=(
            <div>
            <h2><p>We will visualise the Regular Expression Matching problem</p></h2>
            <a href='https://leetcode.com/problems/regular-expression-matching/' target="_blank">Problem statement (leetcode)</a>
            <Rem_Form></Rem_Form>
            </div>
        );
    }
    else{
        compy=(
        <div>
            <h2><p>We will visualise the Edit-Distance problem</p></h2>
            <a href='https://leetcode.com/problems/edit-distance/' target="_blank">Problem statement (leetcode)</a>
            <Ed_Form></Ed_Form>
        </div>
        )
        
    }
    return (
    <div>
        <label><h3>Select your DP problem: </h3></label>
        <select onChange={this.change_handler1}>
            <option value="Longest Common Subsequence">Longest Common Subsequence</option>
            <option value="Regular Expression Matching">Regular Expression Matching</option>
            <option value="Edit Distance">Edit distance</option>
        </select>
        {compy}
    </div>
    );
}


}

export default Multiform;