import React from 'react';
class Ed_Square extends React.Component{
    constructor(props){
        super(props);
    }

    render(){
        let typer;
        let id = `node-${this.props.row}-${this.props.col}`
        if(this.props.typey==='show'){
            typer={color:'black'};
        }
        else if(this.props.typey==='show_string'){
            typer={color:'black',fontWeight:'bold'};
        }
        else{
            typer={color:'white',};
        }
        return (
            <div id={id} className='node' style={typer}> {this.props.val} </div>
        )
    }
}

export default Ed_Square;