import React from 'react';
import Lcs_Memo from './memo'


class lcs_Form extends React.Component {
    constructor(props){
        super(props);
        this.state={
            string_1:'',
            string_2:'',
            submitted:false,
        }
        this.change_handler1 = this.change_handler1.bind(this);
        this.change_handler2 = this.change_handler2.bind(this);
        this.handle_submit=this.handle_submit.bind(this);
    }

change_handler1(event) {
    this.setState({
        string_1: event.target.value,
        submitted:false,
    });

}

change_handler2(event) {
    this.setState({
        string_2: event.target.value,
        submitted:false,
    });
}
handle_submit(e){
    this.setState({
        submitted:true,
    })
    e.preventDefault();
}
render() {
    let compy;
    if(this.state.submitted){
        compy=(
            <div>
            <p style={{fontWeight: 'bold',fontSize:'20px'}}>string 1 is {this.state.string_1}</p>
            <p style={{fontWeight: 'bold',fontSize:'20px'}}>string 2 is {this.state.string_2}</p>
            <Lcs_Memo string_1={this.state.string_1} string_2={this.state.string_2}></Lcs_Memo>
            </div>
        );
    }
    else{
        compy = (<div></div>);
    }
    return (
    <div>
       <h2>Please enter both of your (non-empty) strings</h2>
        <form onSubmit={this.handle_submit}>
            <p><label>String 1 <input type='text' name='s1' value={this.state.string_1} onChange={this.change_handler1}></input></label></p>
             <p><label>String 2 <input type='text' name='s2' value={this.state.string_2} onChange={this.change_handler2}></input></label></p>
             <input type='submit' value='submit' ></input>
        </form>
        {compy}
        <div></div>
    </div>
    );
}


}

export default lcs_Form;